/* CHAT STYLES */
* {
  font-family: 'Open Sans', sans-serif;
  letter-spacing: 0.5px;
}
::-webkit-scrollbar {  width: 0;
  background: transparent;
}
.chat-header{
  background: #014670;
  position: relative;
  max-width: 100%;
  height: 55px; 
}

.chat-logo{
  font-family: 'Circe',sans-serif !important;
  font-size: 38px;
  line-height: 32px;
  color: #fff;
  font-weight: 400;
  margin: 0 25px;
  padding-top: 13px;
}
.btn-logout{
  background-color: #9ACC23;
  border-color: #9ACC23;
  color: #fff;
  border-style: none;
  font-size: 12px;
  padding:8px;
  border-radius: 0.25rem;
  font-weight: bold;
  text-transform: uppercase;
  position: absolute;
  top: 13px;
  right: 22px;
  cursor: pointer;
}
.btn-logout:hover{
  background-color: #769A21;
}
.btn-crearsala{
  background:linear-gradient(331.7deg, #004874 35.42%, #00AFD3 93.78%);
  color: #fff;
  border-style: none;
  font-size: 15px;
  font-weight: 600;
  float: right;
  padding: 10px;
  padding-bottom: 11px;
  cursor: pointer;
  margin-top: 5px;
}
.btn-crearsala:hover{
  background: linear-gradient(72.36deg,#0F3547 1.88%,#004874 53.89%);
}
.label-publica{
  color: #fff;
  font-size: 14px;
  font-weight: bold;
}
.input-publica{
  margin-left: 10px;
  margin-top: 4px;
  border-style: none;
}
.label-sala{
  background: #00AFD3;
  max-width: 100%;
}
.chat-seccion{
  background: #0F3547;;
} 
.sala-input{
  height: 38px;
  width: calc(100% - 114px);
  background-color: #fff;
  border: none;
  padding: 0px 18px;
  outline: none;
  font-size: 15px;
  margin-top: 5px;

}
.ce-chat-list {
  background-color: rgb(240, 240, 240) !important;
}
.ce-chats-container {
  background-color: rgb(240, 240, 240) !important;
}
.ce-active-chat-card {
  background-color: #BFE6EF !important;
  border: 4px solid #BFE6EF !important;
  border-radius: 0px !important;
  box-shadow: 2px 2px 5px rgb(77, 87, 91,0.3);
}
.ce-chat-subtitle-text {
  color: #191d1e !important;
}
.ce-chat-form-container {
  padding-bottom: 20px !important;
}
.ce-text-input {
  border-radius: 6px !important;
  border: 1px solid rgb(77, 87, 91,0.3) !important;
  color: #191d1e;
}

.ce-danger-button {
  background-color: white !important;
  border-radius: 22px !important;
}

.ce-settings {
  background-color: rgb(240, 240, 240) !important;
}

.ce-autocomplete-input {
  font-family: 'Open Sans',sans-serif !important;
  font-style: normal;
  font-size: 13px !important;
  border-radius: 6px !important;
  border: 1px solid rgb(77, 87, 91,0.3) !important;
  color: #191d1e !important;
}

.ce-autocomplete-options {
  border-radius: 6px !important;
  border: 1px solid rgb(77, 87, 91,0.3) !important;
  background-color: white !important;
}

.ce-chat-settings-container {
  padding-top: 12px !important;
  color: #191d1e !important;
}
.ce-options-label{
  border-radius: 6px !important;
  border: 1px solid rgb(77, 87, 91,0.3) !important;
  background-color: white !important;
}
.ce-options-label:hover{
  color:#2478C7 !important;
  background: #e2f1ff !important;
}
.ce-chat-avatars-row {
  display: none !important;
}
.ce-section-title-container{
  border-radius: 6px !important;
  border: 1px solid rgb(77, 87, 91,0.3) !important;
  background-color: white !important;
}
.ce-person-title-container{
  color: #191d1e !important;
}
.ce-section-title-container.ce-person-title-container:hover{
  color:#2478C7 !important;
  background: #e2f1ff !important;
}
/* CUSTOM FEED */

.chat-feed {
  height: 100%;
  width: 100%;
  overflow: scroll;
  background-color: rgb(240, 240, 240);
}

.chat-titulo-container {
  width: calc(100%-36px);
  padding: 18px;
  text-align: center;
}

.chat-titulo {
  color: #014670;
  font-weight: 400;
  font-size: 24px;
  font-family: 'Circe',sans-serif;
  line-height: 32px;
}

.chat-subtitulo {
  font-family: 'Circe',sans-serif;
  color: #014670;
  font-size: 12px;
  padding-top: 4px;
}

.msj-row {
  float: left;
  width: 100%;
  display: flex;
  margin-left: 18px;
}

.msj-block {
  width: 100%;
  display: inline-block;
}

.msj-avatar {
  width: 44px;
  height: 44px;
  border-radius: 22px;
  text-align: center;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 48px;
}

.msj {
  padding: 12px;
  font-size: 16px;
  border-radius: 20px;
  max-width: 100%;
}

.msj-img {
  margin-right: 18px;
  object-fit: cover;
  border-radius: 6px;
  height: 30vw;
  /* width: 30vw; */
  max-height: 200px;
  max-width: 200px;
  min-height: 100px;
  min-width: 100px;
}

