.error{
  color: #fff !important;
  font-size: 24px;
  font-family: 'Circe',sans-serif;
  text-align: center;
  font-weight: 400;
}
.msj-form-container {
  position: absolute;
  bottom: 0px;
  width: calc(100%);
  padding: 18px;
  background-color: rgb(240, 240, 240);
}

.msj-form {
  overflow: hidden;
  border-radius: 0.25rem;
  border: 1px solid rgb(77, 87, 91,0.3);
  background-color: white;
}

.msj-input {
  height: 40px;
  width: calc(100% - 132px);
  background-color: white;
  border: 1px solid white;
  padding: 0px 18px;
  outline: none;
  font-size: 15px;
}
.msj-username{
  position: relative;
  color: #0075b0;
  font-weight: 400;
  font-size: 14px;
  margin-left: 15px;
  padding-bottom: 7px;
}
.image-button {
  cursor: pointer;
  padding: 0px 12px;
  height: 100%;
}

.send-button {
  height: 42px;
  background-color: white;
  border: 1px solid white;
  cursor: pointer;
}

.send-icon {
  top: 1px;
  position: absolute;
  font-size: 20px;
  top: 28.75px;
  right: 35px;
}

.picture-icon {
  top: 1px;
  position: relative;
  font-size: 20px;
}

/* FORM STYLES */
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 62,5%;
}

.wrapper {
  height: 100vh;
	width: 100%;
  background:linear-gradient(331.7deg, #004874 35.42%, #00AFD3 93.78%);
  display: flex;
  justify-content: center;
  align-items: center;
}

.input {
  color: #333;
  font-size: 1.2rem;
	margin: 0 auto;
  padding: 1rem 2rem;
  border-radius: 0.25rem;
  background-color: rgb(255, 255, 255);
  border: none;
  width: 90%;
  display: block;
  border-bottom: 0.3rem solid transparent;
  transition: all 0.3s;
  outline: none;
  margin-bottom: 25px;
}

.form {
  width: 400px;
}

.title {
  font-family: 'Circe',sans-serif;
  text-align: center;
  color: white;
  margin-bottom: 30px;
  width: 100%;
  font-size: 2.3em;;
}

.button {
  border-radius: 0.25rem;
  border: none;
  background-color: white;
  color: #191d1e;
  text-align: center;
  text-transform: uppercase;
  font-size: 14px;
  padding: 15px;
  width: 200px;
  transition: all 0.4s;
  cursor: pointer;
  margin: 5px;
  width: 70%;
}
.button span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.4s;
}
.button span:after {
  content: '\00bb';
  position: absolute;
  opacity: 0;
  top: 0;
  right: -20px;
  transition: 0.5s;
}
.button:hover{
  color:#2478C7 !important;
  background: #e2f1ff !important;
}
.button:hover span {
  padding-right: 25px;
}
.button:hover span:after {
  opacity: 1;
  right: 0;
}